
import { mapGetters } from "vuex";
import { plusready, plusOpen } from "common/plus";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import line from "views/index/module/line";
import picker from "components/tjPicker.vue";
import { queryCarChargingChart } from "api/sysVehicle";

export default {
data() {
return {
chargTime: "2017-04-01 6:00",
cw: null,
alarmData: {},
listData: [["电压", "电流", "温度", "角度"]],
chartData: {
xList: ["00:00"],
yList: [0]
},
title: "电压",
getData: {}
};
},
computed: {
...mapGetters(["direction", "defalutDep"])
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
"curve-line": line,
picker
},
created() {
plusready(this.plusready);
},
methods: {
plusready() {
this.cw = plus.webview.currentWebview();
this.alarmData = this.cw.value;
this.queryCarChargingChart();
},
changeNew(val) {
this.title = val;
this.switch();
},
queryCarChargingChart() {
let { carId, beginTime, endTime } = this.alarmData;
beginTime = beginTime + ":00";
endTime = endTime + ":00";
let data = {
carId,
beginDate:beginTime,
endDate:endTime
};
queryCarChargingChart(data)
.then(res => {
if (res.data && res.data.retX) {
this.getData = res.data;
this.switch();
return;
}
this.chartData = {
xList: ["00:00"],
yList: [0]
};
})
.catch(err => {
this.chartData = {
xList: ["00:00"],
yList: [0]
};
});
},
switch() {
let arr = [];
switch (this.title) {
case "温度":
arr = this.getData.retTemperature;
break;
case "电压":
arr = this.getData.retPowerVbv;
break;
case "电流":
arr = this.getData.retPowerAmpere;
break;
case "角度":
arr = this.getData.retAngle;
break;
}
if (!this.getData.retX) {
this.chartData = {
xList: ["00:00"],
yList: [0]
};
return;
}
this.chartData = {
xList: this.getData.retX,
yList: arr
};
}
}
};



